/**
 * Internal dependencies
 */
import type {
	ActiveExperimentSummary,
	ExperimentSummary,
	Session,
} from '../../types';

export const getActiveExperiments = (
	session: Session
): ReadonlyArray< ActiveExperimentSummary > =>
	Object.values( session.experiments ).filter( isActiveExperiment );

const isActiveExperiment = (
	e: ExperimentSummary
): e is ActiveExperimentSummary => e.active;
