/**
 * External dependencies
 */
import type { Dict } from '@nab/types';

/**
 * Internal dependencies
 */
import { headReady } from '../../../public/utils/helpers';
import { applyAutoInlineExperiments } from '../../../public/alternative-loading/apply-auto-inline-experiments';
import type { ActiveExperimentSummary } from '../../../public/types';

void ( async () => {
	const win = window as unknown as Dict;
	const experiment = win.nabExperiment as ActiveExperimentSummary;
	const alternative = win.nabAlternative as number;
	await applyAutoInlineExperiments( [ experiment ], alternative );
	headReady( () => {
		const style = document.getElementById( 'nelio-ab-testing-overlay' );
		style?.parentNode?.removeChild( style );
	} );
} )();
