/**
 * External dependencies
 */
import type { Maybe } from '@nab/types';

type CookieOptions = {
	readonly path: string;
	readonly expires: number;
};

const COOKIE_NAMES = [
	'nabAlternative',
	'nabCheckWritePermission',
	'nabExperimentsWithPageViews',
	'nabIsStagingSite',
	'nabIsUserLoggedIn',
	'nabIsVisitorExcluded',
	'nabParticipation',
	'nabSegmentation',
	'nabUniqueViews',
	'nabSessionChecksum',
] as const;

type CookieName = ( typeof COOKIE_NAMES )[ number ];

export function setCookie(
	name: CookieName,
	value: number | boolean | string,
	options?: Partial< CookieOptions >
): void {
	const opts: CookieOptions = {
		path: '/',
		expires: 0,
		...( options || {} ),
	};

	let cookie = `${ name }=${ encodeURIComponent( value ) };`;

	if ( 0 < opts.expires ) {
		const expiration = new Date(
			new Date().getTime() + opts.expires * 864e5
		);
		cookie += ` expires=${ expiration.toUTCString() };`;
	} //end if

	cookie += ` path=${ opts.path };`;
	cookie += ' SameSite=None; Secure;';

	document.cookie = cookie;
} //end setCookie()

export function getCookie( name: string ): Maybe< string > {
	const cookies = document.cookie
		.split( ';' )
		.map( ( cookie ) => cookie.trim() )
		.reduce( ( memo, cookie ) => {
			memo[ cookie.split( '=' )[ 0 ] ?? '' ] =
				cookie.split( '=' )[ 1 ] ?? '';
			return memo;
		}, {} as Record< string, string > );

	if ( ! cookies[ name ] ) {
		return;
	} //end if
	return decodeURIComponent( cookies[ name ] ?? '' );
} //end getCookie()

export function removeCookie( name: string ): void {
	document.cookie = `${ name }=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/; SameSite=None; Secure`;
} //end removeCookie()

export function cleanNabCookies(): void {
	COOKIE_NAMES.forEach( removeCookie );
} //end cleanNabCookies()
