/**
 * External dependencies
 */
import md5 from 'md5';
import type { ExperimentId, Maybe } from '@nab/types';

/**
 * Internal dependencies
 */
import { doesVisitorParticipate } from './internal/does-visitor-participate';
import { getSettings } from './internal/get-settings';

import { getCookie, removeCookie, setCookie } from '../cookies';
import { updateSegmentationSettings } from '../segmentation';
import { removeQueryArgs } from '../url';
import {
	getExperimentsWithPageViews,
	getUniqueViews as doGetUniqueViews,
} from '../tracking';
import type { AlternativeIndex, Session, Settings } from '../../types';
import type { Uuid } from 'uuid';

// =======
// HELPERS
// =======

export const getSession = async (): Promise< Maybe< Session > > => {
	const settings = getSettings();
	if ( ! settings ) {
		// eslint-disable-next-line no-console
		console.error( '[NAB] Settings not found.' );
		return;
	} //end if

	if ( ! doesVisitorParticipate( settings ) ) {
		return;
	} //end if

	await updateSegmentationSettings( settings );

	const alternative = updateAlternativeCookie( settings );
	updateStagingCookie( settings );

	const session = {
		...settings,
		alternative,
		currentUrl: window.location.href,
		untestedUrl: removeQueryArgs( window.location.href, 'nab' ),
	};
	await updateECommerceSession( session );

	return session;
};

const updateAlternativeCookie = ( settings: Settings ) => {
	const { maxCombinations } = settings;
	const nabAlt = Number.parseInt( getCookie( 'nabAlternative' ) ?? '' );
	let alternative: number = isNaN( nabAlt ) ? -1 : nabAlt;
	if ( alternative < 0 || alternative >= maxCombinations ) {
		alternative = random( maxCombinations - 1 );
		setCookie( 'nabAlternative', alternative, { expires: 120 } );
		setCookie( 'nabExperimentsWithPageViews', '{}', { expires: 120 } );
	} //end if

	const newAlternative = shouldAlternativeBeOverwritten( alternative );
	if ( false !== newAlternative ) {
		alternative = newAlternative % settings.maxCombinations;
		setCookie( 'nabAlternative', alternative, { expires: 120 } );
		setCookie( 'nabExperimentsWithPageViews', '{}', { expires: 120 } );
		setCookie( 'nabUniqueViews', '{}', { expires: 120 } );
	} //end if

	setCookie( 'nabAlternative', getCookie( 'nabAlternative' ) ?? '', {
		expires: 120,
	} );
	setCookie(
		'nabExperimentsWithPageViews',
		getCookie( 'nabExperimentsWithPageViews' ) ?? '{}',
		{ expires: 120 }
	);

	return alternative;
};

const updateStagingCookie = ( settings: Settings ) =>
	settings.isStagingSite
		? setCookie( 'nabIsStagingSite', true )
		: removeCookie( 'nabIsStagingSite' );

const updateECommerceSession = async ( session: Session ) => {
	if ( ! session.ajaxUrl ) {
		return;
	} //end if

	const oldChecksum = getCookie( 'nabSessionChecksum' );
	const checksum = getSessionChecksum( session.alternative );
	if ( ! session.forceECommerceSessionSync && oldChecksum === checksum ) {
		return;
	} //end if

	try {
		const body = new FormData();
		body.append( 'action', 'nab_sync_ecommerce_session' );
		body.append( 'alternative', `${ session.alternative }` );
		body.append(
			'expsWithView',
			JSON.stringify( getViewedAlternatives( session ) )
		);
		body.append(
			'uniqueViews',
			JSON.stringify( getUniqueViews( session ) )
		);

		await fetch( session.ajaxUrl, {
			method: 'POST',
			credentials: 'same-origin',
			body,
		} );
	} catch ( e ) {}

	setCookie( 'nabSessionChecksum', checksum );
};

const random = ( max: number ) =>
	Math.min( Math.floor( Math.random() * ( max + 1 ) ), max );

const getViewedAlternatives = (
	session: Session
): Record< ExperimentId, AlternativeIndex > =>
	Object.keys( getExperimentsWithPageViews() ).reduce( ( res, key ) => {
		const id = Number.parseInt( key ) || 0;
		const exp = session.experiments[ id ];
		if ( exp ) {
			res[ id ] = session.alternative % exp.alternatives.length;
		} //end if
		return res;
	}, {} as Record< number, number > );

const getUniqueViews = ( session: Session ): Record< ExperimentId, Uuid > => {
	const uniqueViews = doGetUniqueViews();
	return Object.keys( session.experiments ).reduce( ( res, key ) => {
		const id = Number.parseInt( key ) || 0;
		const viewUuid = uniqueViews[ id ];
		if ( viewUuid ) {
			res[ id ] = viewUuid;
		} //end if
		return res;
	}, {} as Record< ExperimentId, Uuid > );
};

const getSessionChecksum = ( alternative: number ): string => {
	const epv = getExperimentsWithPageViews();
	const expIds = Object.keys( epv ).sort();
	const tenMinutesTimestamp =
		new Date( Object.values( epv ).sort().reverse()[ 0 ] || 0 )
			.toISOString()
			.substring( 0, 15 ) + '0';
	return md5( [ alternative, ...expIds, tenMinutesTimestamp ].join( ':' ) );
};

function shouldAlternativeBeOverwritten(
	assignedAlternative: number
): number | false {
	const search = document.location.search;
	if ( ! /\bnabforce\b/.test( search ) ) {
		return false;
	} //end if

	if ( ! /\bnab\b=[0-9]+/.test( search ) ) {
		return false;
	} //end if

	const expectedAlternative =
		Number.parseInt( search.replace( /^.*\bnab\b=([0-9]+).*$/, '$1' ) ) ||
		0;
	if ( expectedAlternative === assignedAlternative ) {
		return false;
	} //end if

	return expectedAlternative;
} //end shouldAlternativeBeOverwritten()
