/**
 * Internal dependencies
 */
import {
	cleanNabCookies,
	getCookie,
	removeCookie,
	setCookie,
} from '../../cookies';
import type { GdprCookieSetting, Session } from '../../../types';

type ParticipationSettings = Pick<
	Session,
	'gdprCookie' | 'participationChance'
>;

export function doesVisitorParticipate(
	session: ParticipationSettings
): boolean {
	if ( ! session ) {
		return false;
	} //end if

	if ( isIE() ) {
		return false;
	} //end if

	if ( ! areCookiesEnabled() ) {
		return false;
	} //end if

	const { gdprCookie } = session;
	if ( ! isGdprAccepted( gdprCookie ) ) {
		cleanNabCookies();
		return false;
	} //end if

	if ( isVisitorExcludedFromTesting() ) {
		return false;
	} //end if

	const { participationChance } = session;
	return doesVisitorParticipateAccordingToTheCookie( participationChance );
} //end doesVisitorParticipate()

// =======
// HELPERS
// =======

function isIE(): boolean {
	const ua = window.navigator.userAgent || '';
	return -1 !== ua.indexOf( 'MSIE ' ) || -1 !== ua.indexOf( 'Trident/' );
} //end isIE()

function isVisitorExcludedFromTesting(): boolean {
	return !! getCookie( 'nabIsVisitorExcluded' );
} //end isVisitorExcludedFromTesting()

function areCookiesEnabled() {
	setCookie( 'nabCheckWritePermission', true );
	if ( ! getCookie( 'nabCheckWritePermission' ) ) {
		return false;
	} //end if

	removeCookie( 'nabCheckWritePermission' );
	return true;
} //end areCookiesEnable();

function isGdprAccepted( gdprCookie: GdprCookieSetting ) {
	if ( ! gdprCookie.name ) {
		return true;
	} //end if

	const matches = ( pattern: string, value: string ) =>
		0 <=
		pattern
			.split( '*' )
			.reduce(
				( start, search ) =>
					start < 0 ? start : value.indexOf( search, start ),
				0
			);

	return document.cookie
		.split( ';' )
		.map( ( c ) => c.trim() )
		.map( ( c ) => c.split( '=' ) )
		.map( ( [ n, ...v ] ) => [ n, v.join( '=' ) ] )
		.filter(
			( c ): c is [ string, string ] =>
				c[ 0 ] !== undefined && c[ 1 ] !== undefined
		)
		.filter( ( [ n ] ) => matches( gdprCookie.name, n ) )
		.some(
			( [ _, v ] ) => ! gdprCookie.value || matches( gdprCookie.value, v )
		);
} //end isGdprAccepted()

function doesVisitorParticipateAccordingToTheCookie(
	participationChance: number
) {
	if (
		! isVisitorParticipationCookieValid() ||
		shouldVisitorParticipationCookieBeUpdated( participationChance )
	) {
		generateVisitorParticipationCookie( participationChance );
	} //end if

	const participation = getCookie( 'nabParticipation' );
	return 0 === participation?.indexOf( 'true' );
} //end doesVisitorParticipateAccordingToTheCookie()

function isVisitorParticipationCookieValid() {
	const participation = getCookie( 'nabParticipation' ) || '';
	return /^(true|false),[0-9]{1,3}$/.test( participation );
} //end isVisitorParticipationCookieValid()

function shouldVisitorParticipationCookieBeUpdated(
	participationChance: number
) {
	const participation = getCookie( 'nabParticipation' ) || '';
	return participation.split( ',' )[ 1 ] !== `${ participationChance }`;
} //end shouldVisitorParticipationCookieBeUpdated()

function generateVisitorParticipationCookie( participationChance: number ) {
	const random = Math.min( 100, Math.floor( Math.random() * 101 ) );
	const shouldVisitorParticipate =
		random <= participationChance ? 'true' : 'false';
	setCookie(
		'nabParticipation',
		`${ shouldVisitorParticipate },${ participationChance }`,
		{ expires: 120 }
	);
} //end generateVisitorParticipationCookie()
