/**
 * Internal dependencies
 */
import { domReady, headReady } from '../utils/helpers';
import type {
	ExperimentSummary as Experiment,
	ActiveExperimentSummary as ActiveExperiment,
	ScriptAlternative,
} from '../types';

export const applyAutoInlineExperiments = async (
	experiments: ReadonlyArray< Experiment >,
	alternative: number
): Promise< void > => {
	await applyAutoInlineExperimentsOn( 'header', experiments, alternative );
	await applyAutoInlineExperimentsOn( 'footer', experiments, alternative );
};

const applyAutoInlineExperimentsOn = (
	inlineLoad: 'header' | 'footer',
	experiments: ReadonlyArray< Experiment >,
	alternative: number
): Promise< void > =>
	new Promise( ( resolve ) => {
		const inlineExperiments = experiments.filter(
			( experiment ): experiment is ActiveExperiment =>
				experiment.active && experiment.inline?.load === inlineLoad
		);

		if ( ! inlineExperiments.length ) {
			return resolve();
		} //end if

		const onReady = 'header' === inlineLoad ? headReady : domReady;
		onReady( async () => {
			await Promise.all(
				inlineExperiments.map( applyExperiment( alternative ) )
			);
			resolve();
		} );
	} );

// ========
// INTERNAL
// ========

const applyExperiment =
	( alternative: number ) =>
	async ( experiment: ActiveExperiment ): Promise< void > => {
		if ( ! experiment.inline ) {
			return;
		} //end if

		switch ( experiment.inline.mode ) {
			case 'unwrap':
				return applyUnwrapExperiment( experiment, alternative );
			case 'visibility':
				return applyVisibilityExperiment( experiment, alternative );
			case 'script':
				return await applyScriptExperiment( experiment, alternative );
		} //end switch
	};

const applyUnwrapExperiment = (
	experiment: ActiveExperiment,
	alternative: number
) => {
	const exp = experiment.id;
	const alt = alternative % experiment.alternatives.length;
	Array.from(
		document.querySelectorAll( `.nab-exp-${ exp }.nab-alt-${ alt }` )
	).forEach(
		( node ) =>
			node.innerHTML.trim() &&
			node.insertAdjacentHTML( 'afterend', node.innerHTML )
	);
	Array.from( document.querySelectorAll( `.nab-exp-${ exp }` ) ).forEach(
		( node ) => node.parentNode?.removeChild( node )
	);
};

const applyVisibilityExperiment = (
	experiment: ActiveExperiment,
	alternative: number
) => {
	const exp = experiment.id;
	const alt = alternative % experiment.alternatives.length;
	Array.from(
		document.querySelectorAll< HTMLElement >(
			`.nab-exp-${ exp }.nab-alt-${ alt }`
		)
	).forEach( ( node ) => {
		node.style.display = '';
	} );
	Array.from( document.querySelectorAll( `.nab-exp-${ exp }` ) ).forEach(
		( node ) => node.parentNode?.removeChild( node )
	);
};

const applyScriptExperiment = (
	experiment: ActiveExperiment,
	alternative: number
): Promise< void > =>
	new Promise( ( resolve ) => {
		if ( ! isScriptExperiment( experiment ) ) {
			return resolve();
		} //end if

		const alt = alternative % experiment.alternatives.length;
		return experiment.alternatives[ alt ]?.run( resolve, {
			domReady,
		} );
	} );

const isScriptExperiment = (
	experiment: ActiveExperiment
): experiment is ActiveExperiment & {
	alternatives: ReadonlyArray< ScriptAlternative >;
} => experiment.inline?.mode === 'script';
